<template>
  <div id="toggle-container" unselectable="on">
    <input type="checkbox" id="darkmode-toggle" v-model="darkModeEnabled" v-on:click="toggle"/>
    <label for="darkmode-toggle">
      <svg class="sun"  viewBox="0 0 24 24" xmlns="http://www.w3.org/2000/svg"><path d="M6.993 12c0 2.761 2.246 5.007 5.007 5.007s5.007-2.246 5.007-5.007S14.761 6.993 12 6.993 6.993 9.239 6.993 12zM12 8.993c1.658 0 3.007 1.349 3.007 3.007S13.658 15.007 12 15.007 8.993 13.658 8.993 12 10.342 8.993 12 8.993zM10.998 19h2v3h-2zm0-17h2v3h-2zm-9 9h3v2h-3zm17 0h3v2h-3zM4.219 18.363l2.12-2.122 1.415 1.414-2.12 2.122zM16.24 6.344l2.122-2.122 1.414 1.414-2.122 2.122zM6.342 7.759 4.22 5.637l1.415-1.414 2.12 2.122zm13.434 10.605-1.414 1.414-2.122-2.122 1.414-1.414z"></path></svg>
      <svg class="moon" viewBox="0 0 24 24" xmlns="http://www.w3.org/2000/svg"><path d="M20.742 13.045a8.088 8.088 0 0 1-2.077.271c-2.135 0-4.14-.83-5.646-2.336a8.025 8.025 0 0 1-2.064-7.723A1 1 0 0 0 9.73 2.034a10.014 10.014 0 0 0-4.489 2.582c-3.898 3.898-3.898 10.243 0 14.143a9.937 9.937 0 0 0 7.072 2.93 9.93 9.93 0 0 0 7.07-2.929 10.007 10.007 0 0 0 2.583-4.491 1.001 1.001 0 0 0-1.224-1.224zm-2.772 4.301a7.947 7.947 0 0 1-5.656 2.343 7.953 7.953 0 0 1-5.658-2.344c-3.118-3.119-3.118-8.195 0-11.314a7.923 7.923 0 0 1 2.06-1.483 10.027 10.027 0 0 0 2.89 7.848 9.972 9.972 0 0 0 7.848 2.891 8.036 8.036 0 0 1-1.484 2.059z"></path></svg>
    </label>
  </div>
</template>

<script>

export default {
  name: "DarkModeToggle",
  data() {
    const darkModeQuery = window.matchMedia("(prefers-color-scheme: dark)");
    return {
      darkModeQuery: darkModeQuery,
      darkModeEnabled: darkModeQuery.matches,

      prefersDarkScheme: darkModeQuery.matches,
    }
  },
  mounted() {
    this.darkModeQuery.addEventListener("change", () => {
      this.prefersDarkScheme = this.darkModeQuery.matches;
      this.darkModeEnabled = this.prefersDarkScheme;

    })

    // this.togglePosition = this.prefersDarkScheme;
  },
  methods: {
    toggle() {
      if (this.prefersDarkScheme) {
        document.body.classList.toggle("light-theme");
      } else {
        document.body.classList.toggle("dark-theme");
      }
      this.darkModeEnabled = this.darkModeQuery.matches;

    }
  },
}
</script>

<style scoped>

label {
  width: 6rem;
  height: 2.5rem;
  position: relative;
  display: block;
  background: rgba(0, 0, 0, 0.1);
  border-radius: 2.5rem;
  box-shadow: inset 0 0.05rem 0.1rem rgba(0, 0, 0, 0.4), inset 0 -0.05rem 0.1rem rgba(255, 255, 255, 0.3);
  cursor: pointer;
  transition: 0.3s;
}

label:after {
  content: "";
  width: 2.1rem;
  height: 2.1rem;
  position: absolute;
  top: 0.2rem;
  left: 0.2rem;
  background: linear-gradient(180deg, #ffcc89, #d8860b);
  border-radius: 1.8rem;
  box-shadow: 0 0.1rem 0.2rem rgba(0, 0, 0, 0.2);
  transition: 0.3s;
}

input {
  width: 0;
  height: 0;
  visibility: hidden;
}

input:checked + label {
  background: rgba(255, 255, 255, 0.1);
}
input:checked + label:after {
  left: 5.8rem;
  transform: translateX(-100%);
  background: linear-gradient(180deg, #777, #3a3a3a);
}

label:hover{
  background: linear-gradient(90deg, rgba(0, 0, 0, 0.01), rgba(255, 255, 255, 0.05));
}
input:checked +label:hover{
  background: linear-gradient(90deg, rgba(0, 0, 0, 0.01), rgba(255, 255, 255, 0.05));
}



@media (hover: hover) and (pointer: fine){
  label:focus:after,
  label:hover:after {
    width: 45%;
  }
}
label:hover:active:after {
  width: 50%;
}

label svg {
  position: absolute;
  height: 1.7rem;
  top: 0.4rem;
  z-index: 100;
}

label svg.sun {
  left: 0.4rem;
  fill: #fff;
  transition: 0.3s;
}

label svg.moon {
  left: 3.9rem;
  fill: #7e7e7e;
  transition: 0.3s;
}

input:checked + label svg.sun {
  fill: #7e7e7e
}

input:checked + label svg.moon {
  fill: #fff
}
#toggle-container{
  position: fixed;
  display: flex;
  top: 4rem;
  right: 2.5rem;
  transition: .3s;
  z-index: 2;

  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  -o-user-select: none;
  user-select: none;

  -webkit-tap-highlight-color: transparent;
}
@media (max-width: 800px) {
  #toggle-container {
    top: 6rem;
    right: 0;
    transform: rotate(90deg);
  }
  label svg.sun,
  label svg.moon {
    transform: rotate(-90deg);
  }
}

@media (max-width: 700px) {
  #toggle-container {
    position: absolute;
    vertical-align: middle;
  }
}
</style>